<template>
  <div class="goodsadd">
    <div class="content-title">
      <div class="titlename">
        <span></span>
      </div>
      <div class="titellink">
      <router-link to="/goods" class="innerbtnlink"></router-link>
      </div>
  </div>
  <div class="content">
    <Form ref="formValidate" :model="Goods" class="goodseditform" :rules="GoodsRule" :label-width="100">
       <Form-item label="" prop="goodsName">
            <Input v-model="Goods.goodsName" placeholder=""></Input>
       </Form-item>
       <Form-item label="" class="text-left">
           <uploader :config="uploaderconfig"> </uploader>
           <input type="hidden" v-model="Goods.goodsImg">
       </Form-item>
       <Form-item label="">
         <a :href="murl + Goods.goodsImg" target="_blank" v-if="Goods.goodsImg">
           <img :src="murl+Goods.goodsImg" alt="" class="goodsimgthumb">
         </a>
      </Form-item>
       <Form-item label="">
            <Input type="textarea" v-model="Goods.goodsDesc" :autosize="{minRows: 2,maxRows: 5}" placeholder="..."></Input>
       </Form-item>
       <Form-item label="" prop="goodsPrice">
            <Input type="text" v-model="Goods.goodsPrice" number></Input>
       </Form-item>
       <Form-item label="" prop="maxPoints">
            <Input type="text" v-model="Goods.maxPoints" number></Input>
       </Form-item>
       <Form-item label=""  prop="storageNum">
            <Input type="text" v-model="Goods.storageNum" number></Input>
       </Form-item>
       <Form-item label="" class="text-left"  prop="goodsStatus">
             <Select v-model="Goods.goodsStatus" style="width:200px">
              <Option value="1"></Option>
              <Option value="2"></Option>
          </Select>
       </Form-item>
       <Form-item label="">
            <Input type="text" v-model="Goods.sort" number></Input>
       </Form-item>
       <Form-item>
            <Button type="primary" @click="handleSubmit('formValidate')"></Button>
            <Button type="ghost" style="margin-left: 8px" @click="handleReset('formValidate')"></Button>
        </Form-item>
    </Form>
  </div>
  </div>
</template>

<script type="text/ecmascript-6">
import uploader from '../Util/Uploader'
export default {
  name: 'GoodsAdd',
  data () {
    return {
      Goods: {
        goodsName: '',
        goodsImg: '',
        goodsPrice: '',
        maxPoints: '',
        sort: 0,
        storageNum: '',
        goodsStatus: 1,
        goodsDesc: '',
        goodsDate: (new Date().getTime())
      },
      GoodsRule: {
        goodsName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        goodsPrice: [
          {type: 'number', min: 0.01, message: '0', trigger: 'blur'}
        ],
        maxPoints: [
          {type: 'number', min: 0, message: '0', trigger: 'blur'}
        ],
        storageNum: [
          {type: 'integer', min: 0, message: '', trigger: 'blur'}
        ]
      },
      uploaderconfig: {
        maxSize: 5120,
        format: ['jpg', 'png', 'jpeg'],
        showUploadList: false,
        parent: 'Goods',
        child: 'goodsImg'
      }
    }
  },
  components: { uploader },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          if (this.Goods.id === '') {
            delete this.Goods.id
          }
          this.http.post('/api/goods', this.Goods).then(res => {
            if (res.error === false) {
              this.$Message.success('')
              this.router.push('/goods')
            }
          })
        } else {
          this.$Message.error('')
        }
      })
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
.goodseditform
  width:60%
  margin-top:30px
.goodsimgthumb
  width:100%
  height:auto
</style>



